import React, { useContext } from 'react'
import {Box, Typography } from '@mui/material'
import BodyPart from './BodyPart'
import { ScrollMenu, VisibilityContext } from 'react-horizontal-scrolling-menu';

import RightArrowIcon from '../assets/icons/right-arrow.png';
import LeftArrowIcon from '../assets/icons/left-arrow.png';
 

// двигаем слайдер вправо-влево с помощью плагина react-horizontal-scrolling-menu
const LeftArrow = () => {
  const { scrollPrev } = useContext(VisibilityContext);
return (
    <Typography onClick={() => scrollPrev()} className="right-arrow">
      <img src={LeftArrowIcon} alt="right-arrow" />
    </Typography>
  );
};

const RightArrow = () => {
  const { scrollNext } = useContext(VisibilityContext);

  return (
    <Typography onClick={() => scrollNext()} className="left-arrow">
      <img src={RightArrowIcon} alt="right-arrow" />
    </Typography>
  );
};

const HorizontalScrollbar = ({bodyParts, bodyPart, setBodyPart }) => {
  return (
   <ScrollMenu LeftArrow={LeftArrow} RightArrow={RightArrow}>
    {
      // массив имен разделов
      bodyParts.map((item, i)=> { 
     return ( 
   <Box
    key={item.id || item}
    itemId={item.id || item} // вот здесь ошибка будет в консоли, НО при сборке уйдет! Если сделать itemID - то в консоли нет ошибки про Invalid DOM property `itemId`. Did you mean `itemID`? - НО  тогда не работает Слайдер!!!!  В общем - в Плагине что то недоработано иил ESLint ругается ??
    title={item.id || item}
    m='0 40px' >
      <BodyPart
   item={item}
   bodyPart={bodyPart}
   setBodyPart={setBodyPart}
   />  
    </Box> 
      )})  
   }
   </ScrollMenu>
  )
}

 
export default HorizontalScrollbar



 